import * as styles from "./styles.css";
import styleSheet from "!css-loader?exportType=css-style-sheet!./styles.css";
class HuluImage extends HTMLElement {
    constructor() {
        super();
        this.shadow = this.attachShadow({ mode: "open" });
        this.shadowRoot.adoptedStyleSheets = [styleSheet];
    }
    static get observedAttributes() {
        return ["src", "is-selected"];
    }
    connectedCallback() {
        this.render();
        this.image = this.shadow.querySelector(".img-wrapper");
        this.loadImage();
    }
    attributeChangedCallback(name, oldValue, newValue) {
        if (oldValue === newValue)
            return;
        if (!this.image)
            return;
        if (name === "is-selected")
            this.image.classList.toggle("selected", newValue === "true");
        else {
            this.render();
            this.loadImage();
        }
    }
    loadImage() {
        const image = this.shadow.querySelector("#background-img");
        if (!image)
            return;
        image.onload = () => {
            image.style.opacity = "1";
        };
    }
    render() {
        const isSelected = this.getAttribute("is-selected") === "true" || false;
        const src = this.getAttribute("src") || "";
        const logo = this.getAttribute("logo") !== "undefined"
            ? this.getAttribute("logo")
            : undefined;
        this.shadow.innerHTML = `
      <div 
        class="${styles["img-wrapper"]} 
        ${isSelected ? styles["selected"] : ""}"
      >
      ${src
            ? `<img class="${styles["img"]}" id="background-img" src=${src} alt="background" />`
            : ""}
      ${logo
            ? `<img class="${styles["logo"]}" id="logo" src=${logo} alt="logo" />`
            : ""}
    </div>`;
    }
}
customElements.define("hulu-image", HuluImage);
