import "../Image";
import * as styles from "./styles.css";
import styleSheet from "!css-loader?exportType=css-style-sheet!./styles.css";
class HuluView extends HTMLElement {
    constructor() {
        super();
        this.shadow = this.attachShadow({ mode: "open" });
        this.shadowRoot.adoptedStyleSheets = [styleSheet];
    }
    connectedCallback() {
        this.render();
    }
    static get observedAttributes() {
        return ["layout", "is-selected", "image", "date", "genre", "type"];
    }
    attributeChangedCallback(name, oldValue, newValue) {
        if (oldValue === newValue)
            return;
        if (name === "is-selected") {
            this.updateSelection(newValue === "true");
        }
        else if (name === "layout") {
            const viewElement = this.shadow.querySelector(".view");
            if (viewElement) {
                viewElement.classList.toggle("vertical", newValue === "vertical");
            }
        }
    }
    adjustScroll() {
        const elementRect = this.getBoundingClientRect();
        const parentElement = this.parentElement;
        console.log(this.shadow.querySelector(".title").innerHTML);
        if (!parentElement)
            return;
        const targetScrollY = window.scrollY + elementRect.top - window.innerHeight / 2;
        const targetScrollX = parentElement.scrollLeft +
            elementRect.left -
            parentElement.clientWidth / 2;
        parentElement.scrollTo({ left: targetScrollX, behavior: "smooth" });
        this.smoothScrollToY(targetScrollY);
    }
    smoothScrollToY(targetY, duration = 400) {
        const startY = window.scrollY;
        const distanceY = targetY - startY;
        const startTime = performance.now();
        function step(currentTime) {
            const elapsed = currentTime - startTime;
            const percentage = Math.min(elapsed / duration, 1);
            // Easing function (easeInOutQuad)
            const ease = percentage < 0.5
                ? 2 * percentage * percentage
                : 1 - Math.pow(-2 * percentage + 2, 2) / 2;
            window.scrollTo(0, startY + distanceY * ease);
            if (elapsed < duration) {
                requestAnimationFrame(step);
            }
        }
        requestAnimationFrame(step);
    }
    updateSelection(isSelected) {
        const imgElement = this.shadow.querySelector("hulu-image");
        const titleElement = this.shadow.getElementById("title");
        const subtitleElement = this.shadow.getElementById("subtitle");
        if (!imgElement || !titleElement || !subtitleElement)
            return;
        if (isSelected) {
            imgElement.setAttribute("is-selected", "true");
            titleElement.classList.add("selected");
            subtitleElement.classList.add("selected");
            this.adjustScroll();
        }
        else {
            imgElement.setAttribute("is-selected", "false");
            titleElement.classList.remove("selected");
            subtitleElement.classList.remove("selected");
        }
    }
    render() {
        const title = this.getAttribute("title");
        const image = this.getAttribute("image");
        const isSelected = this.getAttribute("is-selected") === "true";
        const year = new Date(this.getAttribute("date")).getFullYear();
        const genre = this.getAttribute("genre");
        const logo = this.getAttribute("logo");
        const layout = this.getAttribute("layout");
        this.shadow.innerHTML = `
      <div class="${styles.view} ${layout === "horizontal" ? "" : "vertical"}">
        <hulu-image src=${image} is-selected=${isSelected} logo=${logo}></hulu-image>
        <div class="meta">
          <div id="title" 
            class="${styles["title"]} 
            ${isSelected ? styles["selected"] : ""}">
              ${title}
          </div>
          <div id="subtitle" class="subtitle ${isSelected ? styles["selected"] : ""}">
            ${genre} ● ${year}
          </div>
        </div>
      </div>
    `;
    }
}
customElements.define("hulu-view", HuluView);
